import axios from 'axios'
import {
    DESIGN_LIST_REQUEST,
    DESIGN_LIST_SUCCESS,
    DESIGN_LIST_FAIL,

    DESIGN_DETAILS_REQUEST,
    DESIGN_DETAILS_SUCCESS,
    DESIGN_DETAILS_FAIL,

    DESIGN_DELETE_REQUEST,
    DESIGN_DELETE_SUCCESS,
    DESIGN_DELETE_FAIL,

    DESIGN_CREATE_REQUEST,
    DESIGN_CREATE_SUCCESS,
    DESIGN_CREATE_FAIL,

    DESIGN_UPDATE_REQUEST,
    DESIGN_UPDATE_SUCCESS,
    DESIGN_UPDATE_FAIL,

    DESIGN_CREATE_REVIEW_REQUEST,
    DESIGN_CREATE_REVIEW_SUCCESS,
    DESIGN_CREATE_REVIEW_FAIL,

    


} from '../constants/designConstants'

export const listDesigns = (keyword = '') => async (dispatch) => {
    try{
        dispatch({type: DESIGN_LIST_REQUEST})

        const { data } =await axios.get(`/api/designs${keyword}`)

        dispatch({
            type: DESIGN_LIST_SUCCESS,
            payload: data
        })

    }catch(error){
        dispatch({
            type: DESIGN_LIST_FAIL,
            payload: error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message
        })
    }
}
export const listDesignDetails = (id) => async (dispatch) => {
    try{
      dispatch({ type: DESIGN_DETAILS_REQUEST }) 
      const {data}  = await axios.get(`/api/designs/${id}`)

      dispatch({
        type: DESIGN_DETAILS_SUCCESS,
        payload: data
      })

    } catch( error )
    {
        dispatch({
            type: DESIGN_DETAILS_FAIL,
            payload: error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
        })
    }
}

export const createDesignReview = (designId, review) => async (dispatch, getState) => {
    try {
        dispatch({
            type: DESIGN_CREATE_REVIEW_REQUEST
        })

        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.post(
            `/api/designs/${designId}/reviews/`,
            review,
            config
        )
        dispatch({
            type: DESIGN_CREATE_REVIEW_SUCCESS,
            payload: data,
        })



    } catch (error) {
        dispatch({
            type: DESIGN_CREATE_REVIEW_FAIL,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}