import React, {useEffect} from 'react'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import {Row, Col, ListGroup, Image, 
    //Form, 
    Button, Card } from 'react-bootstrap'
//import Message from '../components/Message'
import {addToCart,removeFromCart} from '../actions/cartActions'
import { useNavigate } from "react-router-dom"
import { useParams } from 'react-router-dom'
import DeleteIcon from '@mui/icons-material/Delete'
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
function CartScreen({match}) {
  const [itemCount, setItemCount] = React.useState(1);
  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin
  const designId = useParams().id
  const dispatch = useDispatch()
  const history=useNavigate()
  const cart = useSelector(state => state.cart ) || { cartItems: [] }
  const { cartItems } = cart || { cartItems: [] }
  console.log("userInfo: ",userInfo)
  useEffect(() => {
        if(designId){
            dispatch(addToCart(designId))
        }
    },[dispatch,designId])
  
    const removeFromCartHandler = (id) => {
      setItemCount(Math.max(itemCount - 1, 0));
      dispatch(removeFromCart(id))
  }
    const myDesignDetailsHandler = (id) =>
    {
        dispatch()
    }
  const checkoutHandler = () => {
      history('/login?redirect=shipping')
  }
 // if(userInfo !== null)
            return (
                
                <Row>
                <Col md={8}>
                    <h1>Shopping Cart</h1>
                    {cartItems.length === 0 ? ( 
                            <h3>Your cart is empty <Link to='/'>Go Back</Link></h3>
                    
                            ) : (
                                
                                <ListGroup variant='flush'>
                                    {cartItems.map(item => (
                                        <ListGroup.Item key={item.design}>
                                            <Row>
                                                <Col md={2}>
                                                    <Image src={item.image} alt={item.name} fluid rounded />
                                                </Col>
                                                <Col md={3}>
                                                    <Link to={`/design/${item.design}`}>{item.name}</Link>
                                                </Col>

                                                <Col md={2}>
                                                    ${item.price}
                                                </Col>
                            
                                                <Col md={2}>
                                                   < DeleteIcon onClick={() => removeFromCartHandler(item.design)} />
                                                    
                                                </Col>

                                                <Col md={2}> <InfoOutlinedIcon onClick={() => myDesignDetailsHandler(item.design)} />
                                                    
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                    ))}
                                </ListGroup>
                                )}
                </Col>

                <Col md={4}>
                    <Card>
                        <ListGroup variant='flush'>
                        
                            <ListGroup.Item>
                                    <h2>Subtotal ({cartItems.reduce((acc, item) => acc + 1, 0)}) items</h2>
                                    ${cartItems.reduce((acc, item) => acc + 1 * item.price, 0).toFixed(2)}
                            </ListGroup.Item>

                            <ListGroup.Item>
                                <Button
                                    type='button'
                                    className='btn-block'
                                    disabled={cartItems.length === 0}
                                    onClick={checkoutHandler}           >
                                    Proceed To Checkout
                                </Button>
                            </ListGroup.Item>

                    </ListGroup>
                    </Card>
                </Col>
            </Row>
            )
  //  else
    //return (<h2 className='danger'>Please log in to show your shopping cart</h2>)


}

export default CartScreen
