import {CART_ADD_ITEM,CART_REMOVE_ITEM, CLEAR_CART} from '../constants/cartConstants'


const initialState = {
    cartItems: []
  }

export const cartReducer = (state = { cartItems : [] }, action) => {
    switch(action.type)
    {
        case CART_ADD_ITEM:
            const item     = action.payload
            const exisItem = state.cartItems.find(x => x.design === item.design) 
            if(exisItem){
                return{
                    ...state,
                    cartItems: state.cartItems.map(x =>
                        x.design === exisItem.design ? item : x )
                }

            }else{
                return{
                    ...state,
                    cartItems:[...state.cartItems, item]
                }
            }
        case CART_REMOVE_ITEM:
            return{
                ...state,
                cartItems: state.cartItems.filter(x => x.design !== action.payload)
            }
        case CLEAR_CART:
            return initialState
            
        default:
            return state
    }
}