//<Route path='/admin/user/:id/edit'  element={<UserEditScreen/>}  />
//<Route path='/admin/designlist'      element={<ProductListScreen/>}  />
//<Route path='/admin/desig/:id/edit'  element={<ProductEditScreen/>}  />
export default App;
