import React,{
  //useState, 
  useEffect } from 'react'
import {useDispatch, useSelector } from 'react-redux'
import {Row,Col} from 'react-bootstrap'
import Design from '../components/Design'
import {listDesigns} from '../actions/designActions'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { useLocation } from 'react-router-dom'

function HomeScreen() {

  const dispatch = useDispatch()
  const designList = useSelector (state => state.designList )
  const { error, loading, designs } = designList
  let keyword = useLocation.search

  useEffect(() => {
      dispatch(listDesigns())
    
  },[dispatch,keyword])

  return (
    <div> 
      <h1>Latest Designs</h1>
      {loading ? <Loader />
         : error ? <Message variant='danger'>{error}</Message>
            :
            <Row>
              {designs.map(design => (
                <Col key={design._id} sm={12} md={6} lg={4} xl={3}>
                  <Design design={design}/>
                </Col>
          ))}
          </Row>
    }
     
    </div>
  )
}

export default HomeScreen
