import React, {
  useState, 
  useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {Link} from 'react-router-dom'
import {Row,Col,Image,ListGroup, ListGroupItem,Card,Form, Button} from 'react-bootstrap'
import Rating from '../components/Rating'
import { useNavigate
//  ,useMatch 
} from "react-router-dom";
import { useParams } from 'react-router-dom';
import {listDesignDetails, createDesignReview} from '../actions/designActions'
//import { DESIGN_CREATE_REVIEW_RESET } from '../constants/designConstants'

function DesignScreen() {
  const [itemCount, setItemCount] = React.useState(1);
  const {id} = useParams()
  const [rating, setRating] = useState(0)
  const [comment, setComment] = useState('')
  const history= useNavigate()
  const dispatch = useDispatch()
  const designDetails = useSelector(state => state.designDetails)
  const { loading, error, design } = designDetails
  useEffect(() => {

    dispatch(listDesignDetails(id))

  }, [dispatch])

  const addToCartHandler = ( ) =>{
      setItemCount(itemCount + 1);
      history(`/cart/${id}`)
  }
  return (
    <div>
      <Link to='/' className='btn btn-light my-3'>Go Back</Link>
      <Row>
          <Col md={6}>
            <Image src={design.image} alt={design.name} fluid />
          </Col>

          <Col md={3} className='h-100'>
            <ListGroup variant='flush'>
               
              <ListGroupItem> <h3>{design.name}</h3> </ListGroupItem>
              <ListGroupItem> <Rating value={design.rating} text={`${design.numReviews} reviews`} color={'#f8e825'}/> </ListGroupItem>
              <ListGroupItem> Category: {design.category} </ListGroupItem>
              <ListGroupItem> Font: {design.font} </ListGroupItem>
              <ListGroupItem> Style: {design.style} </ListGroupItem>
              <ListGroupItem> Careated at:{design.createdAt} </ListGroupItem>
              <ListGroupItem><strong>Price:</strong> ${design.price}   </ListGroupItem>
              <ListGroupItem>Description: {design.description}</ListGroupItem>
            </ListGroup>
          
          </Col> 

          <Col md={3}>
            <Card>
              <ListGroup>
                <ListGroupItem>
                  <Row>
                    <Col>Price: </Col>
                    <Col>
                        <strong>${design.price}</strong>
                    </Col>
                  </Row>
                </ListGroupItem>

                <ListGroup.Item>
                    <Button    onClick={addToCartHandler}     className='btn-block' type='button'>
                        Add to Cart
                    </Button>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
           
      </Row>
    </div>
  )
  }
export default DesignScreen
